import React, { useState, useEffect } from "react";
import UserNav from "../../components/nav/UserNav";
import { getUserOrders, changeStatus } from "../../functions/user";
import { useSelector, useDispatch } from "react-redux";
import { CheckCircleOutlined, CloseCircleOutlined } from "@ant-design/icons";
import { toast } from "react-toastify";
import ShowPaymentInfo from "../../components/cards/ShowPaymentInfo";
import { PDFDownloadLink } from "@react-pdf/renderer";
import Invoice from "../../components/order/Invoice";

const History = () => {
  const [orders, setOrders] = useState([]);
  const { user } = useSelector((state) => ({ ...state }));

  useEffect(() => {
    loadUserOrders();
  }, []);

  const loadUserOrders = () =>
    getUserOrders(user.token).then((res) => {
      // console.log(JSON.stringify(res.data, null, 4));
      setOrders(res.data);
    });

  const showOrderInTable = (order) => (
    <table className="table table-bordered">
      <thead className="thead-light">
        <tr>
          <th scope="col">Title</th>
          <th scope="col">Price</th>
          <th scope="col">Brand</th>
          <th scope="col">Color</th>
          <th scope="col">Count</th>
          <th scope="col">Shipping</th>
        </tr>
      </thead>

      <tbody>
        {order.products.map((p, i) => (
          <tr key={i}>
            <td>
              <b>{p.product.title}</b>
            </td>
            <td>{p.product.price}</td>
            <td>{p.product.brand}</td>
            <td>{p.color}</td>
            <td>{p.count}</td>
            <td>
              {p.product.shipping === "Yes" ? (
                <CheckCircleOutlined style={{ color: "green" }} />
              ) : (
                <CloseCircleOutlined style={{ color: "red" }} />
              )}
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );

  const showDownloadLink = (order) => (
    <PDFDownloadLink
      document={<Invoice order={order} />}
      fileName="invoice.pdf"
      className="btn btn-sm btn-raised text-dark grad-button"
    >
      Download PDF
    </PDFDownloadLink>
  );

  const cancelOrder = (orderId) => {
    if (window.confirm("Are you sure want to cancel order ?")) {
      let orderStatus = "Cancelled"
      changeStatus(orderId, orderStatus, user.token)
        .then((res) => {
          // console.log("ORDER CANCELLED--->", res)
          toast("Order Cancelled");
          loadUserOrders();
        })
        .catch((err) => {
          // console.log("CANCEL ERR--->", err)
        })
    }

  }

  const showEachOrders = () =>
    orders.map((order, i) => (
      <div key={i} className="m-5 p-3 card">
        <ShowPaymentInfo order={order} />
        {showOrderInTable(order)}
        <div className="row">
          <div className="col">
            {order.orderStatus !== "Cancelled" && order.orderStatus !== "Completed" && (
              <button className="btn btn-raised btn-danger" onClick={() => { cancelOrder(order._id) }}>cancel order</button>
            )}

          </div>
        </div>

        <div className="row">
          <div className="col">{showDownloadLink(order)}</div>
        </div>
      </div>
    ));

  return (
    <div className="container-fluid mt-5">
      <div className="row ">
        <div className="col-md-2">
          <UserNav />
        </div>
        <div className="col-md-10 text-center">
          <h2 className=" shaw text-secondary mt-4">
            {orders.length > 0 ? "User purchase orders" : "No purchase orders"}
          </h2>
          {showEachOrders()}
        </div>
      </div>
    </div>
  );
};

export default History;
